  grid-template-columns: repeat(9, 1fr);
  grid-auto-rows: minmax(100px, auto);
  grid-template-areas: "hd hd hd hd hd hd hd sd sd" "main main main main main main main main main" ". ft ft ft ft ft ft ft .";
