@media (min-width: 768px) {
  .wrapper {
    grid-template-columns: repeat(9, 1fr);
    grid-template-areas: "hd hd hd hd hd hd hd hd hd" "main main main main main main main main main" "sd sd sd sd sd sd sd sd sd" ". ft ft ft ft ft ft ft .";
  }
