.p-calendar {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto 1fr;
  grid-gap: 2rem;
  height: 100vh;
  border: 1rem solid saddlebrown;
  padding: 2rem;
  box-sizing: border-box;
  font-family: "Bree Serif", serif;
  font-size: 1.8rem;
}

.p-calendar h1,
.p-calendar h2 {
  margin: 0;
  padding: 0;
}

.p-calendar header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto auto;
}

.p-calendar #year {
  grid-row: 1;
  grid-column: 2;
  justify-self: center;
  font-family: "Candal", sans-serif;
  font-size: 3rem;
}

.p-calendar #month {
  grid-row: 2;
  grid-column: 2;
  justify-self: center;
  font-family: "Candal", sans-serif;
}

@media (max-width: 767px) {
  .p-calendar #month {
    font-size: 2.5rem;
  }
}

@media (min-width: 768px) {
  .p-calendar #month {
    font-size: 3rem;
  }
}

@media (min-width: 768px) {
  .p-calendar #month {
    font-size: 5rem;
  }
}

.p-calendar #prev,
.p-calendar #next {
  grid-row: 1 / span 2;
  align-self: center;
}

.p-calendar button {
  border: 5px solid #be946a;
  color: #d78822;
  border-radius: 2rem;
  padding: 15px;
  margin: 0px 20px;
  background: #d8bc9d;
  font-size: 1.5rem;
}

.p-calendar button:hover {
  background: #be946a;
  cursor: pointer;
}

.p-calendar button:active {
  border: 5px solid #a9abbe;
  color: #a9abbe;
  background: #d78822;
  position: relative;
  top: 2px;
  left: 2px;
}

.p-calendar nav {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-gap: 10px;
  justify-items: center;
}

.p-calendar #abr {
  display: none;
}

.p-calendar main {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-auto-rows: 1fr;
  grid-gap: 10px;
}

.p-calendar .red {
  color: #a52430;
}

.p-calendar .day {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  align-items: end;
  justify-items: end;
  padding: 5px 10px;
  background: #d8bc9d;
}

.p-calendar .red.day {
  background: #be946a;
}

.p-calendar .today {
  color: #5d628f;
  font-weight: bold;
}

@media (min-width: 768px) {
  .p-calendar {
    font-size: 2rem;
  }
  .p-calendar #days {
    display: none;
  }
  .p-calendar #abr {
    display: grid;
  }
  .p-calendar button {
    border: 3px solid #be946a;
    border-radius: 15px;
    padding: 10px;
    margin: 0px 10px;
    font-size: 1.2em;
  }
}

@media (min-width: 50px) {
  .p-calendar {
    font-size: 5rem;
  }
  .p-calendar main {
    grid-template-columns: repeat(7, 1fr);
  }
  .p-calendar button {
    border: 2rem solid #be946a;
    border-radius: 3rem;
    padding: 5rem;
    margin: 0 1rem;
    font-size: 3rem;
  }
}

.p-fallbacks {
  font-size: 2rem;
  background: whitesmoke;
  color: #333;
}

@media (min-width: 768px) {
  .p-fallbacks {
    padding: 3rem;
  }
  .p-fallbacks p {
    margin: 3rem 0;
  }
}

.p-fallbacks h1 {
  font-size: 4rem;
}

.p-fallbacks h2 {
  font-size: 3rem;
  margin: 3rem 0;
}

.p-fallbacks h3 {
  font-size: 2.5rem;
  margin: 3rem 0;
}

.p-fallbacks p {
  display: block;
  margin: 3rem 0;
}

#float-fallback {
  margin: 10rem auto;
}

#float-fallback .grid > div {
  float: left;
}

#float-fallback .grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, auto);
  width: 500px;
}

#float-fallback .grid > div {
  background-color: #694486;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
}

#inline-block-fallback {
  margin: 10rem auto;
}

#inline-block-fallback .grid > div {
  display: inline-block;
}

#inline-block-fallback .grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, auto);
  width: 500px;
}

#inline-block-fallback .grid > div {
  background-color: #694486;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
}

#table-fallback {
  margin: 10rem auto;
}

#table-fallback .grid > div {
  display: table-cell;
  vertical-align: top;
}

#table-fallback .grid {
  border-spacing: 10px;
}

#table-fallback .grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, auto);
  width: 500px;
}

#table-fallback .grid > div {
  background-color: #694486;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
}

#item-widths-fallback {
  margin: 10rem auto;
}

#item-widths-fallback .grid > div {
  float: left;
  width: 33.333%;
}

@supports (display: grid) {
  #item-widths-fallback .grid > div {
    width: auto;
  }
}

#item-widths-fallback .grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, 1fr);
  width: 500px;
}

#item-widths-fallback .grid > div {
  background-color: #694486;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
}



